@use "global" as *;

.p-how {
  padding-top: rem(32);
  padding-bottom: rem(40);
  position: relative;
  @include mq(md) {
    padding-top: rem(112);
    padding-bottom: rem(70);
  }
}
.p-how::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  background: #e9ea93;
  z-index: -1;
}
.p-how__content {
  max-width: rem(1047);
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  border-radius: rem(25);
  margin-top: rem(24);
  @include mq(md) {
    margin-top: rem(-40);
  }
}
.p-how__title {
  width: 100%;
  position: relative;
  margin-left: auto;
  margin-right: auto;
  max-width: rem(250);
  @include mq(md) {
    max-width: rem(387);
  }
}
.p-how__title::before {
  padding-top: 22.967%;
  display: block;
  content: "";
}
.p-how__title img {
  position: absolute;
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.p-how__detail {
  background-color: $white;
  padding-top: rem(48);
  padding-left: rem(24);
  padding-right: rem(24);
  padding-bottom: rem(24);
  margin-top: rem(-28);
  border-radius: rem(20);
  @include mq(md) {
    background: url(../images/common/how-wrapper-background.png);
    background-position: center;
    background-size: 100%;
    background-repeat: no-repeat;
    padding-top: rem(80);
    padding-bottom: rem(43);
    padding-left: rem(52);
    padding-right: rem(52);
    max-width: rem(1047);
    width: 100%;
    margin-top: rem(-48);
  }
}
.p-how__description {
  text-align: center;
  padding-bottom: rem(32);
  background-image: linear-gradient(to right, $red 4px, transparent 4px);
  background-size: 12px 4px;
  background-repeat: repeat-x;
  background-position: right bottom;
}
.p-how__annotation {
  margin-top: rem(28);
  text-align: center;
}
.p-how__btn {
  margin-top: rem(60);
  text-align: center;
}
.p-how__link {
  display: inline-block;
  width: rem(300);
  @include mq(md) {
    width: rem(382);
  }
}
